<template>
  <div>
    <div class="container">
      <div class="row justify-content-center sm:p-4" v-if="td.length > 0">
        <div class="col-12">
          <br />
          <p class="p-3 my-2 text-center text-2xl font-bold font-italic bg-white  mt-3 rounded-lg shadow">SURAT SERING DIBACA</p>
          <div class="row">
            <div class="col-sm-6 ">
              <div class="input-group rounded-lg shadow mb-2 bg-white p-4">
                <div v-if="listPencarian.length>0">
                    <p class="font-semibold p-2">Index Pencarian : </p>
                    <v-select :options="listPencarian" style="min-width:300px;" label="title" v-model="cari" :reduce="e => e.value"></v-select>
                </div>
              </div>
            </div>
            <div class=" col-sm-6 pl-3 pr-3">
              <div class="bg-white  rounded-lg shadow p-4">
                <p class="font-semibold p-2">Ukuran huruf : </p>
                <div class="sm-form ">
                  <select
                    class="form-control text-sm"
                    style="height: 34px; width: 200px"
                    v-model="pilihanLebar"
                  >
                    <option
                      v-for="(item, index) in lebar"
                      :key="index"
                      :value="item"
                    >
                      {{ index + 1 }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          class="col-12 col-sm-12 mt-3 p-2 cursor-pointer"
          @click="
            pilih = item;
            show = !show;
          "
          v-for="(item, index) in td"
          :key="index + 'asmaulhusna'"
        >
          <div class="rounded-lg shadow bg-white p-5 sm:p-5">
            <p class="pb-3 text-3xl font-italic">
              {{ item.title }}
            </p>
            <p
              class="pb-3 text-center"
              :class="`${pilihanLebar}`"
              style="font-family: 'Amiri', serif; line-height: 2"
            >
              {{ item.arabic }}
            </p>
            <p class="pt-2 text-sm">
              {{ item.translation }}
            </p>
          </div>
        </div>
        <div class="col-12">
          <p class="p-3 text-center text-lg font-bold font-italic">
            Sumber : {{ detail.source }} <br />
            <br />
            Based_on {{ detail.based_on }}
          </p>
        </div>
      </div>
      <div v-else>
        <div
          style="height: 90vh"
          class="d-flex justify-content-center align-items-center"
        >
          <div style="width: 100%">
            <p
              class="text-center animated bounce infinite text-lg font-semibold"
            >
              Loading ...
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  layout: "app_islamic",
  computed: {
    td() {
      let that = this;
      let data = this.datanya;
      data = data.filter((e) => {
        if (e.title.toString().toLowerCase().indexOf(that.cari.toLowerCase()) != -1) {
          return e;
        }
      });
      return data;
    },
  },
  methods: {
    getData() {
      axios
        .get("https://islamic-api-zhirrr.vercel.app/api/tahlil")
        .then((res) => {
          console.log(JSON.stringify(res.data.data));
          this.datanya = this.tahlil;
          this.tahlil.forEach(e=>{
            this.listPencarian.push({title:e.title,value:e.title})
          });
          this.detail = res.data;
          // console.log(res.data)
          this.$forceUpdate();
        });
    },
  },
  mounted() {
     let that=this;
     function onBackKeyDown(e){
          e.preventDefault();
          e.stopPropagation();
          that.$router.push('/app/islamic')
        };
        document.addEventListener("backbutton", onBackKeyDown, false);
    this.getData();
  },
  data() {
    return {
      cari: "",
      pencarian:'',
      datanya: [],
      detail: {},
      show: false,
      pilih: {},
      lebar: ["text-lg", "text-xl", "text-2xl", "text-4xl", "text-5xl"],
      pilihanLebar: "text-2xl",
      listPencarian:[{title:'All',value:''}],
      tahlil: [
        {
          id: 12,
          title: "Ayat Kursi (Surat Al-Baqarah ayat 255)",
          arabic:
            "اللهُ لاَ اِلَهَ اِلاَّ هُوَ الْحَىُّ الْقَيُّومُ، لاَ تَاْ خُذُهُ سِنَةٌ وَلاَ نَوْمٌ، لَّهُ مَا فِى السَّمَوَاتِ وَمَا فِى الْاَرْضِ، مَنْ ذَا الَّذِى يَشْفَعُ عِنْدَهُ اِلاَّ بِاِذْنِهِ، يَعْلَمُ مَا بَينَ اَيْدِيْهِمِ وَمَا خَلْفَهُمْ، وَلاَ يُحْيِطُونَ بِشَيْءٍ مِّنْ عِلْمِهِ اِلاَّ بِمَا شَاءَ، وَسِعَ كُرْسِيُّهُ السَّمَوَاتِ وَالْاَرْضَ، وَلاَ يَئُودُهُ حِفْظُهُمُا، وَهُوَ الْعَلِىُّ الْعَظِيْمُ",
          translation:
            "Allah, tiada yang layak disembah kecuali Dia yang hidup kekal lagi berdiri sendiri. Tidak mengantuk dan tidak tidur. Milik-Nya apa yang ada di langit dan di bumi. Tiada yang dapat memberikan syafa’at di sisi-Nya kecuali dengan izin-Nya. Dia mengetahui apa yang ada di hadapan dan di belakang mereka. Mereka tidak mengetahui sesuatu dari ilmu-Nya kecuali apa yang dikehendaki-Nya. Kursi Allah meliputi langit dan bumi. Dia tidak merasa berat menjaga keduanya. Dia maha tinggi lagi maha agung",
        },
      ],
    };
  },
  metaInfo: {
    title: `Baca Al Qur'an`,
    link: [
      { rel: "preconnect", href: "https://fonts.googleapis.com" },
      { rel: "preconnect", href: "https://fonts.gstatic.com" },
      {
        rel: "stylesheet",
        href: "https://fonts.googleapis.com/css2?family=Amiri&display=swap",
      },
    ],
  },
};
</script>